const API = {
	production: '',
	development: 'http://127.0.0.1:8086/api'
};
let apiUrl = "", token = "";
const urls = {
	//
	production: {
		blogList: 'https://api.aihongchen.cn/api/blogList',
	},
	localHost: {},
	//Mock
	development: {
		blogList: '/blogList/',
	}

};
const expUrl = urls[process.env.NODE_ENV]; //url

if (process.env.NODE_ENV == 'development') {
	/*onlineurldev*/
	for (var key in urls.production) {

		if (!expUrl[key]) {
			expUrl[key] = urls.production[key];
		} else {
			expUrl[key] = API[process.env.NODE_ENV] + expUrl[key];
		}
	}
}
export default {
	...expUrl
}



